<template>
    <div class="checkComment">
        <div class="comment-content">
            <div class="comment-item" v-for="(item,index) in commentProd" :key="index">
                <div class="left-item left-box">
                    <div class="info">
                        <img :src="item.smallImg" alt="" class="img">
                        <p class="order-info">{{item.chrName}}</p>
                        <div class="block">
                            <span class="demonstration">{{$L('综合评分')}}</span>
                            <el-rate v-model="item.meanScore" disabled></el-rate>
                        </div>
                    </div>
                </div>
                <div class="right-item right-box">
                    <div class="desc">{{item.chrContent}}</div>
                    <ul class="img-group" >
                        <li class="li" v-for=" k in item.pic" v-if="item.hasOwnProperty('pic')">
                            <img class="img" :src="k" alt="">
                        </li>
                    </ul>
                    <p>{{item.dtCreateTime}}</p>
                    <br/>
                    <p>
                        {{ $L('商家回复') }}:
                        <span v-if="item.hasOwnProperty('merchantReview') && item.merchantReview"> {{ item.merchantReview }} </span>
                        <span v-else> {{ $L('暂未回复') }} </span>
                    </p>
                </div>


            </div>
            <div class="comment-bottom">
                <div class="left-box">{{ $L('网站动态评分') }}</div>
                <div class="right-box">
                    <p class="p"><span class="red">*</span>{{$L(' 宝贝与描述相符')}}<el-rate v-model="myScore.scoreDesc" disabled></el-rate></p>
                    <p class="p"><span class="red">*</span>{{$L(' 卖家的服务态度')}}<el-rate v-model="myScore.scoreService" disabled></el-rate></p>
                    <p class="p"><span class="red">*</span>{{$L(' 物流服务的质量')}}<el-rate v-model="myScore.scoreKuaiDi" disabled></el-rate></p>
                </div>
            </div>
            <div class="btns">
                <el-button @click="backFunc">{{ $L('返回') }}</el-button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "checkComment",
        data(){
            return {
                loading:true,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                rate:4.5,
                commentProd:{},
                avgScore:{},
                myScore:{},
                imgList:[
                    {imgItem:require('../../../assets/img/common/default_user.jpg')},
                    {imgItem:require('../../../assets/img/common/default_user.jpg')},
                    {imgItem:require('../../../assets/img/common/default_user.jpg')},
                ],
                dialogImageUrl: '',
                dialogVisible: false,
                product:3,
                service:5,
                logistics:4
            }
        },
        created() {
            let id = 0;
            if (this.$route.params.hasOwnProperty('id')) {
                id = this.$route.params.id;
                id = parseInt(id);
            }
            if (!isNaN(id) && id) {
                this.getData(id);
            } else {
                this.showError();
            }
            this.getData(id);
        },
        methods:{
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            handleRemove(file, fileList) {

            },
            handlePictureCardPreview(file) {
                this.dialogImageUrl = file.url;
                this.dialogVisible = true;
            },
            backFunc() {
                this.$router.back({name:'orderList',params:{type:'all'}});
            },
            getData(id) {
                const _self = this;
                _self.loading = true;
                _self.$api('commentList', {orderId: id}, function (reData) {
                    if (reData.flag === 1) {
                        _self.commentProd = reData.data.commentList;
                        _self.avgScore = reData.data.avgScore;
                        _self.myScore = reData.data.myScore;
                        _self.loading = false;

                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('重试'),
                            cancelButtonText: _self.$L('返回'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData(id);
                        }).catch(action => {
                            _self.backFunc();
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('重试'),
                        cancelButtonText: _self.$L('返回'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData(id);
                    }).catch(action => {
                        _self.backFunc();
                    });
                });
            },
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/lib.scss";
    .checkComment{
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 100;
        padding: 20px !important;
        .comment-content{
            font-size: 14px;
            background-color: #fff;
            padding: 20px;
            .comment-item{
                padding: 40px 0 40px 20px;
                border-bottom: 1px dashed #cccccc;
                overflow: hidden;
                .left-item{
                    .info{
                        text-align: center;
                        .order-info{
                            margin-top: 5px;
                            @extend .text-one-line;
                        }
                        .img{
                            width: 100px;
                            height: 100px;
                        }
                        .block{
                            .demonstration{
                                padding: 15px 0 5px;
                                display: inline-block;
                            }
                        }
                    }
                }
                .right-item{
                    .desc{
                        font-size: 14px;
                        width: 80%;
                    }
                    .img-group{
                        display: flex;
                        margin: 20px 0;
                        .li{
                            margin-right: 10px;
                            .img{
                                width: 60px;
                                height: 60px;
                            }
                        }
                    }
                }
            }
            .comment-bottom{
                overflow: hidden;
                padding: 30px 20px;
                border-bottom: 1px dashed #cccccc;
                .p{
                    margin-bottom: 10px;
                    .el-rate{
                        display: inline-block;
                        vertical-align: middle;
                        margin-left: 10px;
                    }
                }
            }
            .btns{
                text-align: right;
                padding: 40px 30px;
                .el-button{
                    width: 90px;
                    height: 35px;
                }
            }
            .left-box{
                float: left;
                width: 250px;
                text-align: center;
            }
            .right-box{
                float: left;
                width: calc(96% - 250px);
                margin-left: 4%;
            }
            .red{
                color: #FF0000;
            }
        }
        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #FFFFFF;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }
    }
</style>